<template>
  <div class="step-two">
    <div class="mb-5">
      <!-- 1.   -->
      <div v-if="isLoading" style="font-size: 20px;height: 600px;">
        <div class="text-center">
          <div><i class="mdi mdi-loading mdi-spin"></i></div>
          <small>  </small>
        </div>
      </div>

      <!-- 2.    -->
      <div v-else-if="canPhoto">
        <!--    -->
        <div v-if="rows <= columns" class="camera-horizontal camera-frame d-flex">
        <!-- , ,   -->
        <div style="position: relative; width: 1080px; height: 1620px;">
            <!--    -->
            <div class="test-custom" :style="overlayStyle"></div>

            <!--     -->
            <video v-show="!isPhotoTaken" ref="camera" width="1080" height="1620" autoplay></video>
            <canvas v-show="isPhotoTaken" id="photoTaken" ref="canvas" width="1080" height="1620"></canvas>
        </div>

          <div class="col-custom h-100 text-center d-flex align-items-center justify-content-center" style="width: 50px; color: #FFF; font-size: 20px;">
            <div v-if="isPhotoTaken"><br><br><br><br><br><br><br><br><br><br><br>!</div>
            <div v-else-if="getImageLen < 6">{{6 - getImageLen}}<br><br><br><br><br><br><br>!</div>
            <div v-else><br><br><br><br><br><br><br><br><br><br><br>?</div>
          </div>

          <div v-if="!isShotPhoto" class="col-custom" style="width: 100px;">
            <div class="w-100">
              <div class="d-flex justify-content-center align-items-center" style="height: 75px">
                <i v-if="isPhotoTaken" class="mdi mdi-trash-can" style="font-size: 30px;color: #FFF;" @click="isPhotoTaken=false"></i>
              </div>
              <div class="d-flex justify-content-center align-items-center" style="height: 300px;">
                <div v-if="getImageLen < 6" class="takePic d-flex justify-content-center align-items-center"
                  v-on="isPhotoTaken ? { click: () => { saveImage(); isPhotoTaken = false; } } : { click: () => { takePhoto(); } }">
                  <div class="takePic-inner">
                    <div v-if="isPhotoTaken" class="h-100 d-flex justify-content-center align-items-center">
                      <i class="mdi mdi-tray-plus" style="font-size: 20px;"></i>
                    </div>
                  </div>
                </div>
                <div v-else class="takePic d-flex justify-content-center align-items-center" style="font-size: 25px;">
                  <i class="mdi mdi-restore" @click="isOpen = true;"></i>
                </div>
              </div>
              <div style="height: 75px;"></div>
            </div>
          </div>
        </div>

        <!--    -->
        <div v-else class="camera-vertical camera-frame">
          <div class="d-flex justify-content-center align-items-center" style="height: 50px;color: #FFF;font-size: 20px;">
            <div v-if="isPhotoTaken">   </div>
            <div v-else-if="getImageLen < 4">{{4 - getImageLen}}  !</div>
            <div v-else>  ?</div>
          </div>
          <div>
            <video v-show="!isPhotoTaken" ref="camera" width="1080" height="1620" autoplay></video>
            <canvas v-show="isPhotoTaken" id="photoTaken" ref="canvas" width="1080" height="1620"></canvas>
          </div>
        </div>
      </div>

      <!-- 3.    -->
      <div v-else class="m-auto" style="height: 1620px; width: 1080px;">
        <div v-if="!getImageLen" class="h-100 card" @click="onUploadClick">
          <div class="m-auto">
            <i class="mdi mdi-arrow-up-bold" style="font-size: 70px;"></i>
            <div><strong> </strong></div>
          </div>
        </div>
        <div v-else class="m-auto h-100">
          <div class="h-100" style="box-shadow: 1px 1px 3px black;">
            <div class="m-auto row h-50" v-for="(row, rowIdx) of [0, 1]" :key="rowIdx">
              <div class="col-4 m-auto" v-for="(col, colIdx) of [0, 1, 2]" :key="colIdx">
                <div v-if="Object.values(images)[rowIdx*3 + col]" class="card m-auto mb-3" style="position: relative;">
                  <img class="uploadImage" :src="`${Object.values(images)[rowIdx*3 + col]}`">
                  <div class="overlay">
                    <i class="mdi mdi-close" @click="removeImg(Object.keys(images)[rowIdx*3 + col])"></i>
                  </div>
                </div>
                <div v-else class="text-center card" @click="onUploadClick">
                  <i class="mdi mdi-arrow-up-bold" style="font-size: 60px;"></i>
                  <div><strong> </strong></div>
                </div>
              </div>
            </div>
          </div>
          <div v-if="getImageLen == 6" class="text-center">
            <button class="btn-custom" @click="initImage"></button>
          </div>
        </div>
        <div class="text-center" v-if="getImageLen < 6">     6 !</div>
        <input ref="fileInput" @change="onImageUpload" type="file" style="display: none;" multiple>
      </div>
    </div>

    <modal v-if="isOpen" @on-close="isOpen=false" @on-submit="isOpen=false;initImage()"
           :title="' '" :msg="'    ?'" />
  </div>
</template>


<script>
import Modal from '../../components/modal.vue'

export default {
    name: 'StepOne',
    components: {
        Modal
    },
    data() {
        return {
            images: {},
            rows: 2,
            columns: 1,
            canPhoto: false,
            isOpen: false,
            isCameraOpen: false,
            isPhotoTaken: false,
            isShotPhoto: false,
            isLoading: false,
            isTarget: null,
            overlayIndex: 1, //    
            overlayImages: [
            require('@/assets/frame/y1.png'),
            require('@/assets/frame/y2.png'),
            require('@/assets/frame/y3.png'),
            require('@/assets/frame/y4.png'),
            require('@/assets/frame/t1.png'),
            require('@/assets/frame/t2.png'),
            require('@/assets/frame/t3.png'),
            require('@/assets/frame/t4.png'),
            ],
            frameName: null,
        }
    },
    created() {
        
    },
    mounted() {
        let table = this.$store.getters.getFrame
        console.log(table)
        this.rows = 2; //table.split('x')[0];
        this.columns = 2; //table.split('x')[1];
        console.log(this.rows, this.columns)
        this.images = this.$store.getters.getImages;

        if (this.getImageLen == 6) this.$store.commit('setNext', true);
        else this.$store.commit('setNext', false);

        const frameName = this.$store.getters.getFrameName;
        if(frameName == 't'){
          this.overlayIndex = 5;
        }else if(frameName == 'y'){
          this.overlayIndex = 1;
        }else{
          this.overlayIndex = 10;
        }
        console.log('  dd:', this.overlayIndex);

        this.createCameraElement();
    },
    beforeDestroy() {
        // this.showingImage();
        // this.stopCameraStream();
    },
    methods: {
        createCameraElement() {
            this.isLoading = true;
            let camSize = null
            this.rows <= this.columns ? camSize = { width: 400, height: 300 } : camSize = { width: 300, height: 400 };

            const constraints = (window.constraints = {
                audio: false,
                video: {
                    height: camSize.height,
                    width: camSize.width,
                },
            });

            navigator.mediaDevices.getUserMedia(constraints)
                .then(stream => {
                    this.isLoading = false;
                    this.canPhoto = true;

                    return stream;
                })
                .then(stream => {
                    this.$refs.camera.srcObject = stream;
                })
                .catch(error => {
                    console.error(error)
                    this.isLoading = false;
                    this.canPhoto = false;
            });
        },

        stopCameraStream() {
            let tracks = this.$refs.camera.srcObject.getTracks();

            tracks.forEach(track => {
                track.stop();
            });
        },

/*
        takePhoto() {
            if (!this.isPhotoTaken) {
                this.isPhotoTaken = true;
            } else return;

            const context = this.$refs.canvas.getContext('2d');

            this.rows <= this.columns ? context.drawImage(this.$refs.camera, 0, 0, 600, 450) : context.drawImage(this.$refs.camera, 0, 0, 450, 600);
        },
*/
        takePhoto() {
        if (!this.isPhotoTaken) {
            this.isPhotoTaken = true;
        } else return;

        const canvas = this.$refs.canvas;
        const context = canvas.getContext('2d');

        const width = 1080;
        const height = 1620;

        // 1.   
        context.drawImage(this.$refs.camera, 0, 0, width, height);

        // 2.    
        const overlayImg = new Image();
        overlayImg.crossOrigin = "anonymous"; // 
        
        overlayImg.src = this.overlayImages[this.overlayIndex - 1];
        console.log(overlayImg.src);
        overlayImg.onload = () => {
            context.drawImage(overlayImg, 0, 0, width, height);
        };
        },
        saveImage() {
            const image = this.$refs.canvas.toDataURL("image/jpeg").replace("image/jpeg", "image/octet-stream");
            let id = (new Date).getTime();

            this.$set(this.images, id, image);

            const frameName = this.$store.getters.getFrameName;
            console.log('  ddss:', frameName);
            console.log('  ddss:', this.overlayIndex);

            if(frameName == 'y'){
                //   
                if(this.overlayIndex <= 3){
                  this.overlayIndex++;
                }else{
                  this.overlayIndex = 10;
                }
            }else if(frameName == 't'){              
                if(this.overlayIndex <= 8){
                  this.overlayIndex++;
                }
            }
        },

        showingImage() {
            this.$store.commit('setImages', this.images);
        },
        
        initImage() {
            this.$store.commit('setNext', false);
            this.$store.commit('setImages', {});
            this.$store.commit('setTargets', {});
            this.$store.commit('setImgCanvas', {});
            this.$store.commit('setTmpTargets', {});
            this.$store.commit('setUpdateQueue', []);
            
            this.images = {};
        },

        removeImg(target) {
            this.images[target] = null;
            this.$delete(this.images, target);
            this.$store.commit('setImages', this.images);
        },

        onUploadClick() {
            this.$refs.fileInput.click();
        },

        async onImageUpload(e) {
            if (this.getImageLen >= 6) return;

            let files = Array.from(e.target.files)

            for await (let file of files) {
                let id = (new Date).getTime();

                if (this.getImageLen >= 6) break;
                this.$set(this.images, id, await this.readFile(file));
            }
        },

        readFile(file) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.onload = (e) => {
                    resolve(e.target.result);
                }
                reader.readAsDataURL(file);
            })
        },
    },
    computed: {
        getImageLen() {
            return Object.keys(this.images).length;
        },
        overlayStyle() {
            return {
            backgroundImage: `url(${this.overlayImages[this.overlayIndex - 1]})`,
            backgroundSize: 'cover',
            backgroundPosition: 'top center',
            backgroundRepeat: 'no-repeat'
            };
        },
    },
    watch: {
        images: {
            deep: true,
            handler() {
                if (this.getImageLen == 6) this.$store.commit('setNext', true);
                this.showingImage();
            }
        }
    }
}
</script>

